.bikes {

  &__images {
    @include useGrid(6, 24px);

    @include onDesktop {
      @include useGrid(12, 24px);
    }

    @include onPhone {
      display: block;
    }
  }
  
  &__image {
    position: absolute;
    height: 100%;
    width: 100%;

    &-container {
      position: relative;
      width: 100%;

      grid-column: span 4;

      max-height:318px;
      box-sizing: border-box;

      &::after {
        padding-bottom: 100%;
        content: "";
        display: block;
      }
    }

    &__image-details {
      grid-column: auto;
    }
  }

  &__title {
    padding-top: 16px;
    padding-bottom: 8px;
  
    font-weight: 700;
    font-size: 24px;
    line-height: 140%;
  
    color: $c-white;
  }

  &__description {
    font-weight: 400;
    font-size: 16px;
    line-height: 140%;
  
    color: $c-white;
  }
}



