.page {
  font-family: "Poppins", sans-serif;
  background-color: $c-black;

  &__body {
    margin: 0;
    height: 100vh;
    width: 100vw;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);

    &:target {
    transform: translateX(0);

    transition: transform .3s;
    }
  }

  &__section {
    margin-bottom: 120px;
    color: $c-white;

    &-h2 {
      font-weight: 500;
      font-size: 48px;
      line-height: 100%;
      letter-spacing: -1.5px;
    }

    &-h4 {
      font-weight: 500;
      font-size: 23px;
      line-height: 140%;
    }

    &-title {
      font-weight: 500;
      font-size: 48px;
      line-height: 100%;
      letter-spacing: -1.5px;
      text-align: center;
      padding-bottom: 0;

      @include onDesktop {
        font-size: 64px;
      }

      @include onPhone {
        font-size: 32px;
      }


    }
  }
}
